<template>
  <main class="content container" v-if="$store.state.isCartLoading">
    <PreloaderDotsWave/>
  </main>
  <main class="content container" v-else-if="cartLoadingFailed">
    Не удалось загрузить корзину
    <button @click.prevent="loadCart">
      Попробовать загрузить ещё раз
    </button>
  </main>
  <main class="content container" v-else>
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name: 'main'}">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name: 'cart'}">
            Корзина
          </router-link>
        </li>
      </ul>

      <h1 class="content__title">
        Корзина
      </h1>
      <span class="content__info">
        {{amountTypes}} товара(-ов)
      </span>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST">
        <div class="cart__field">
          <ul class="cart__list">
            <CartItem v-for="item in products" :key="item.productId" :item="item" />
          </ul>
        </div>

        <div class="cart__block">
          <p class="cart__desc">
            Мы&nbsp;посчитаем стоимость доставки на&nbsp;следующем этапе
          </p>
          <p class="cart__price">
            Итого: <span>{{ totalPrice | numberFormat }} ₽</span>
          </p>

          <router-link tag="button"
                       v-if="amountTypes > 0"
                       :to="{name: 'order'}"
                       class="cart__button button button--primery">
            Оформить заказ
          </router-link>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import numberFormat from '@/helpers/numberFormat';
import { mapActions, mapGetters } from 'vuex';
import CartItem from '@/components/CartItem.vue';
import PreloaderDotsWave from '@/components/PreloaderDotsWave.vue';

export default {
  name: 'CartPage',
  data() {
    return {
      cartLoadingFailed: false,
    };
  },
  filters: { numberFormat },
  components: { PreloaderDotsWave, CartItem },
  computed: {
    ...mapGetters({
      products: 'cartDetailProducts',
      totalPrice: 'cartTotalPrice',
      amountTypes: 'cartProductTypesAmount',
    }),
  },
  methods: {
    ...mapActions({ loadCartAction: 'loadCart' }),

    loadCart() {
      this.cartLoadingFailed = false;

      this.loadCartAction()
        // eslint-disable-next-line no-return-assign
        .catch(() => {
          // this.cartLoading = false;
          this.cartLoadingFailed = true;
        });
    },
  },
  created() {
    this.loadCart();
  },
};
</script>

