<template>
  <li class="cart__item product">
    <div class="product__pic">
      <img :src="item.product.image" width="120" height="120" :alt="item.product.title">
    </div>
    <h3 class="product__title">
      {{ item.product.title }}
    </h3>
    <span class="product__code">
      Артикул: {{ item.product.id }}
    </span>

    <Counter :amount.sync="amount" class="product__counter form__counter"/>

    <b class="product__price">
      {{ item.amount * item.product.price | numberFormat }} ₽
    </b>

    <button class="product__del button-del" type="button"
            aria-label="Удалить товар из корзины"
            @click.prevent="deleteItem"
    >
      <svg width="20" height="20" fill="currentColor">
        <use xlink:href="#icon-close"></use>
      </svg>
    </button>
  </li>
</template>

<script>
import numberFormat from '@/helpers/numberFormat';
import { mapActions } from 'vuex';
import Counter from '@/components/Counter.vue';

export default {
  components: { Counter },
  props: ['item'],
  filters: { numberFormat },
  data() {
    return {
      itemDeleting: false,
      itemDeletingFailed: false,
    };
  },
  computed: {
    amount: {
      get() {
        return this.item.amount;
      },
      set(value) {
        this.$store.dispatch('updateCartProductAmount', {
          productId: this.item.productId,
          amount: value,
        });
      },
    },
  },
  methods: {
    ...mapActions({
      deleteProduct: 'deleteCartProduct',
    }),
    deleteItem() {
      this.itemDeletingFailed = false;
      this.itemDeleting = true;

      this.deleteProduct({ productId: this.item.product.id })
        .then(() => {
          this.itemDeleting = false;
        })
        // eslint-disable-next-line no-return-assign
        .catch(() => this.itemDeletingFailed = true);
    },
  },
};
</script>

